.news-notice {
  box-sizing: border-box;
  padding: 72px 40px 0;

  text-align: center;

  @media (max-width: $desktop-width - 81) {
    padding: 62px 24px 0;
  }

  @media (max-width: $tablet-width - 1) {
    padding: 46px 16px 0;
  }
}

.news-notice__wrap {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-bottom: 48px;

  @media (max-width: $desktop-width - 81) {
    flex-direction: column;
  }

  @media (max-width: $tablet-width - 1) {
    padding-bottom: 30px;
  }
}

.news-notice__news,
.news-notice__notice {
  width: calc(50% - 32px);

  text-align: left;

  @media (max-width: $desktop-width - 81) {
    width: 100%;
  }
}

.news-notice__news {
  @media (max-width: $desktop-width - 81) {
    padding-bottom: 64px;
  }

  @media (max-width: $tablet-width - 1) {
    padding-bottom: 48px;
  }
}

.news-notice__title--news::before {
  background-image: url("../img/icon-news.svg");
}

.news-notice__title--notice::before {
  background-image: url("../img/icon-notification.svg");
}

.news-notice__list {
  margin: 0;
  padding: 0;
  padding-top: 16px;

  list-style: none;

  @media (max-width: $tablet-width - 1) {
    padding-top: 0;
  }
}

.news-notice__item {
  padding-top: 26px;

  border-bottom: 1px solid var(--color-stroke);

  @media (max-width: $tablet-width - 1) {
    padding-top: 24px;
  }
}

.news-notice__date {
  position: relative;

  width: fit-content;
  margin: 0;
  padding: 0;
  padding-left: 24px;

  font-size: rem(14px);
  line-height: 1.3;
  color: var(--color-text-caption);

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;

    width: 16px;
    height: 16px;

    background-image: url("../img/icon-time.svg");
    background-repeat: no-repeat;
  }

  &--notice::after {
    content: "";
    position: absolute;
    top: 1px;
    right: -29px;

    width: 12px;
    height: 12px;

    background-image: url("../img/icon-notification-small.svg");
    background-repeat: no-repeat;
  }
}

.news-notice__text {
  margin: 0;
  padding: 0;
  padding-top: 16px;
  padding-bottom: 32px;

  font-size: rem(16px);
  line-height: 1.35;

  @media (max-width: $tablet-width - 1) {
    padding-bottom: 27px;
  }

  &--link {
    display: block;

    text-align: left;

    @include font-h5;
  }
}

.news-notice__link {
  display: block;
  height: 2.7em;
  overflow: hidden;
  text-overflow: ellipsis;

  @media (max-width: $desktop-width - 81) {
    height: auto;
  }
}

.news-notice__button {
  &--news {
    width: 360px;
    margin-top: 24px;
    margin-bottom: 40px;

    @media (max-width: $mobile-max-width) {
      width: 100%;
      margin-bottom: 32px;
    }
  }
}

.news-notice--news {
  padding: 0;

  & .news-notice__list {
    padding-top: 6px;
  }

  & .news-notice__date {
    display: block;
  }
}
