.news-item__title {
  max-width: 100%;
  margin-bottom: 40px;

  @media (max-width: $mobile-max-width) {
    margin-bottom: 32px;
  }
}

.news-item__subtitle {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 22px;

  @media (max-width: $mobile-max-width) {
    flex-direction: column;
    margin-bottom: 12px;
  }

  & span {
    @include font-h5;
    color: var(--color-text-caption);
    margin-right: 32px;
    margin-bottom: 12px;

    @media (max-width: $mobile-max-width) {
      margin-right: 0;
    }
  }

  & b {
    color: var(--color-text-main);
    font-weight: 400;
  }
}

.news-item__text {
  max-width: 740px;
  padding-bottom: 40px;

  background-image: linear-gradient($light-grey, $light-grey);
  background-position: left bottom;
  background-repeat: no-repeat;
  background-size: 100% 1px;

  &--line {
    max-width: 100%;
    padding-right: 380px;
    padding-bottom: 48px;

    @media (max-width: $desktop-width - 1) {
      padding-right: 0;
    }

    @media (max-width: $tablet-width - 1) {
      padding-bottom: 40px;
    }
  }

  & p {
    margin: 0;
    margin-bottom: 22px;

    @include main-text;
    line-height: rem(27px);

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.news-item__list {
  @include list-reset;
  margin-top: 48px;

  @media (max-width: $mobile-max-width) {
    margin-top: 40px;
  }
}

