.news-item__bottom {
  border: 1px solid $light-grey;
  border-radius: 5px;

  @media (max-width: $mobile-max-width) {
    padding-top: 18px;
    padding-bottom: 16px;
  }
}

.news-item__bottom-link {
  display: block;
  padding: 24px 23px;
  border-radius: 5px;
}

.news-item__button {
  padding-left: 24px;
  padding-right: 24px;
  margin-top: 40px;
  margin-bottom: 120px;

  font-size: rem(14px);
  line-height: rem(19px);

  @media (max-width: $tablet-width - 1) {
    margin-bottom: 88px;
  }

  @media (max-width: $mobile-max-width) {
    width: 100%;
    margin-bottom: 64px;
  }
}

.news-item__process {
  margin-bottom: 40px;

  @media (max-width: $mobile-max-width) {
    margin-top: 22px;
    margin-bottom: 32px;
  }
}
