.filter__open-btn {
  display: none;
}

.filter__header {
  margin-bottom: 36px;
}

.filter__header-item {
  display: inline-block;
  margin: 0;
  font-weight: 400;
  font-size: rem(40px);
  line-height: rem(47px);

  &--last {
    margin-right: 20px;
  }
}

.filter__category-select {
  display: inline-block;
  margin-bottom: 2px;
}

.filter__main {
  padding: 24px 32px;
  background-color: var(--color-bg-secondary);
  border: 1px solid var(--color-stroke);
  border-radius: $border-radius;
}

.filter__main-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-top: -16px;
  margin-left: -32px;
}

.filter__item {
  margin-bottom: 0;
  margin-top: 16px;
  margin-left: 32px;

  &--separated {
    margin-left: auto;
  }
}

.filter__vote-select {
  margin-left: 90px;

  & .ss-main .ss-multi-selected,
  .ss-main .ss-single-selected,
  .ss-main .ss-single-selected .placeholder,
  .ss-main .ss-single-selected .placeholder .ss-disabled {
    height: 20px;
    font-size: rem(15px);
    line-height: rem(20px);
  }

  & .ss-main .ss-content {
    width: 146px;
    right: auto;
    left: 50%;
    transform: translateX(-50%);
  }

  & .ss-main .ss-content .ss-list {
    width: 146px;
    height: auto;
  }

  & .ss-main .ss-single-selected .ss-arrow span {
    width: 8px;
    height: 8px;
  }
}

.filter__show-btn {
  max-width: 200px;
  padding: 10px 20px;
  margin-top: 20px;
}

.filter--js .filter__show-btn {
  display: none;
}


@include tablet {
  .filter__main {
    padding: 25px 24px;
  }

  .filter__vote-select {
    margin-left: 32px;
  }
}


@include mobile {
  .filter__category-select {
    display: block;
    margin-top: 12px;
    margin-bottom: 0;
  }

  .filter__header {
    margin-bottom: 34px;
  }

  .filter__header-item {
    font-size: rem(30px);
    line-height: rem(36px);
  }

  .filter__open-btn {
    display: block;
    width: 100%;
    max-width: 360px;
    margin: 0 auto 24px;
  }

  .filter--js .filter__body {
    display: none;
  }

  .filter--open .filter__body {
    display: block;
  }

  .filter__item {
    &--separated {
      margin-left: 32px;
    }
  }
}
